.hero__box {
  width: 100%;
  margin-bottom: 40px;
}

@media screen and (min-width: 768px) {
  .hero__box {
    flex-direction: row-reverse;
    gap: 32px;
    margin-bottom: 120px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .hero__box {
    margin-bottom: 120px;
  }
}

